<template>
  <el-tabs stretch class="display-card-tabs-style" v-show="showComponents">
    <el-tab-pane label="基础组件" name="0">
      <div
        v-for="(components, index) in nestedComponentArray"
        :key="index"
        class="component-line-wrap"
      >
        <draggable
          class="component-item-container"
          :list="nestedComponentFiles[index]"
          :options="{group:{name:'component', pull:'clone', put: false}}"
          :disabled="drag"
          @start="drag=true"
          @end="drag=false"
        >
          <div
            class="component-item-wrapper"
            v-for="(component, index) in components"
            :key="index"
            @click="showComponentMenu(index)"
          >
            <div class="component-item">
              <img :src="component.image" />
              <span>{{ component.desc }}</span>
            </div>
          </div>
        </draggable>

        <div class="item-menu" v-if="componentMenuObject.selectedIndex === index">
          <div class="menu-action-wrapper">
            <div class="inverted-triangle left-item" :style="triangleStyleObject">
              <div class="inverted-triangle-cover"></div>
            </div>
            <div class="menu-action" @click="hideComponentMenu">
              <span>收起</span>
              <img src="@/assets/icon/default_packup.png" alt />
            </div>
          </div>
          <div class="menu-content">
            <div class="search-bar">
              <input type="text" placeholder="搜索关键词" />
              <img src="@/assets/icon/compose_search_default.png" alt />
            </div>
            <div class="filter-condition">
              <div class="field-wrapper">
                <div class="field-title">
                  <span>选择：</span>
                  <span>全部</span>
                </div>
                <div class="field-content">
                  <span>最新</span>
                  <span>热门</span>
                </div>
              </div>
              <div class="field-wrapper">
                <div class="field-title">
                  <span>行业：</span>
                  <span>全部</span>
                </div>
                <div class="field-content">
                  <span>教育</span>
                  <span>电商</span>
                  <span>企业</span>
                </div>
                <div class="show-more">
                  <span>更多</span>
                </div>
              </div>
              <div class="field-wrapper">
                <div class="field-title">
                  <span>类型：</span>
                  <span>全部</span>
                </div>
                <div class="field-content">
                  <span>商品列表</span>
                  <span>表单</span>
                </div>
                <div class="show-more">
                  <span>更多</span>
                </div>
              </div>
            </div>
            <div class="component-preview-wrapper">
              <draggable
                :options="{group:{name:'component', pull:'clone', put: false}}"
                :disabled="drag"
                @start="drag=true"
                @end="drag=false"
                :list="otherTypeComponentFiles"
              >
                <!-- comp需要单独包装一层以兼容Viewport格式 -->
                <component
                  class="component-preview-item"
                  v-for="(item,index) in componentMenuObject.content"
                  :key="index"
                  :is="item.id"
                  :comp="{componentInfo:item.componentInfo}"
                ></component>
              </draggable>
            </div>
          </div>
        </div>
      </div>
    </el-tab-pane>

    <el-tab-pane label="组合模块" name="1">
      <draggable
        class="component-item-container"
        :list="complexComponentFiles"
        :options="{group:{name:'component', pull:'clone', put: false}}"
        :disabled="drag"
        @start="drag=true"
        @end="drag=false"
      >
        <div
          class="component-item-wrapper"
          v-for="(component) in complexComponents"
          :key="component.name"
        >
          <div class="component-item">
            <img :src="component.image" />
            <span>{{ component.desc }}</span>
          </div>
        </div>
      </draggable>
    </el-tab-pane>
  </el-tabs>
</template>

<script>
import draggable from "vuedraggable";
import componentData from "@/assets/mock/baseComponent.dev.js";
import { createNamespacedHelpers } from "vuex";
const { mapGetters, mapState, mapMutations } = createNamespacedHelpers(
  "menuStore"
);
export default {
  name: "displayCard",
  components: {
    draggable
  },
  data() {
    return {
      drag: false,
      singleComponents: componentData.single,
      complexComponents: componentData.complex,
      componentMenuObject: {
        status: false,
        selectedIndex: null,
        content: []
      },
      triangleStyleObject: {
        left: "40px"
      },
      verticalStyleObject: {
        top: "110px"
      },
      // 垂直偏移量是固定的，乘于倍数即可
      topOffsetValue: 110,
      // 水平方向的Item共三个，所以只需要保存三个偏移量
      leftOffsetValue: [40, 150, 260]
    };
  },
  methods: {
    changeTriangleStyle(index) {
      let horizontalMultiple = index % 3;
      this.triangleStyleObject = {
        left: `${this.leftOffsetValue[horizontalMultiple]}px`
      };
    },
    changeVerticalMenuStyle(index) {
      let verticalMultiple = Math.floor(index / 3) + 1;
      this.verticalStyleObject = {
        top: `${verticalMultiple * this.topOffsetValue}px`
      };
    },
    showComponentMenu(index) {
      if (!this.componentMenuObject.status) {
        this.changeTriangleStyle(index);
        this.changeVerticalMenuStyle(index);
        this.componentMenuObject = {
          status: true,
          selectedIndex: index,
          content: this.singleComponents[index]["otherTypes"]
        };
        console.log(this.componentMenuObject);
      }
    },
    hideComponentMenu() {
      this.componentMenuObject = {
        status: false,
        selectedIndex: null,
        content: []
      };
    }
  },
  computed: {
    // TODO FIX 无法混入两个及以上的getters函数
    ...mapGetters(["showDisplayCard"]),
    ...mapState(["activeMenuIndex", "componentMenu"]),
    nestedComponentArray() {
      return this.singleComponents.reduce((pre, cur, index) => {
        if (index % 3 === 0) {
          pre.push([cur]);
        } else {
          pre[pre.length - 1].push(cur);
        }
        return pre;
      }, []);
    },
    showComponents() {
      return this.activeMenuIndex === 0;
    },
    nestedComponentFiles(){
      return this.singleComponents.reduce((pre,cur,index) => {
        let component = {
          componentInfo: cur.componentInfo,
          // id为Component的文件ID
          file: cur.id,
          styleMenuID: cur.styleMenuID,
          desc: cur.desc,
          image: cur.image,
          name: cur.name
        };
        if (index % 3 === 0) {
          pre.push([component]);
        } else {
          pre[pre.length - 1].push(component);
        }
        return pre;
      },[])
    },
    componentFiles() {
      return this.singleComponents.reduce((pre, cur) => {
        pre.push({
          componentInfo: cur.componentInfo,
          // id为Component的文件ID
          file: cur.id,
          styleMenuID: cur.styleMenuID,
          desc: cur.desc,
          image: cur.image,
          name: cur.name
        });
        return pre;
      }, []);
    },
    complexComponentFiles() {
      return this.complexComponents.reduce((acc, cur) => {
        acc.push({
          componentInfo: cur.componentInfo,
          // id为Component的文件ID
          file: cur.id,
          styleMenuID: cur.styleMenuID,
          desc: cur.desc,
          image: cur.image,
          name: cur.name
        });
        return acc;
      }, []);
    },
    otherTypeComponentFiles() {
      return this.componentMenuObject.content.reduce((acc, cur) => {
        acc.push({
          componentInfo: cur.componentInfo,
          // id为Component的文件ID
          file: cur.id,
          styleMenuID: cur.styleMenuID
        });
        console.log(acc);
        return acc;
      }, []);
    }
  },
  mounted() {
    // 生产环境
    //  this.singleComponents.forEach(item => {
    //    if(item.path){
    //      loadJs(item.path).then(res => {
    //        let component = window.Vue.extend(window[item.id]["default"])
    //        window.Vue.component(item.id, component)
    //         // 注册完成后进行清除
    //        delete window[item.id]
    //      })
    //    }
    //    if(item.styleModificationBoxPath){
    //       loadJs(item.styleModificationBoxPath).then(res => {
    //        let component = window.Vue.extend(window[item.styleMenuID]["default"])
    //        window.Vue.component(item.styleMenuID, component)
    //        // 注册完成后进行清除
    //        delete window[item.styleMenuID]
    //      })
    //    }
    //  })
    // 开发环境
    this.singleComponents.forEach(async item => {
      if (item.path) {
        let component = await item.path();
        window.Vue.component(item.id, window.Vue.extend(component.default));
      }
      if (item.otherTypes) {
        item.otherTypes.forEach(async item => {
          let component = await item.path();
          window.Vue.component(item.id, window.Vue.extend(component.default));
        });
      }
      if (item.styleModificationBoxPath) {
        let component = await item.styleModificationBoxPath();
        window.Vue.component(
          item.styleMenuID,
          window.Vue.extend(component.default)
        );
      }
    });
    this.complexComponents.forEach(async item => {
      if (item.path) {
        let component = await item.path();
        window.Vue.component(item.id, window.Vue.extend(component.default));
      }
      if (item.otherTypes) {
        item.otherTypes.forEach(async item => {
          let component = await item.path();
          window.Vue.component(item.id, window.Vue.extend(component.default));
        });
      }
      if (item.styleModificationBoxPath) {
        let component = await item.styleModificationBoxPath();
        window.Vue.component(
          item.styleMenuID,
          window.Vue.extend(component.default)
        );
      }
    });
  }
};
</script>

