import Vue from "vue";
import util from "@/util/index.js";
const { uuid } = util;
const _ = require("lodash");

export default {
  namespaced: true,
  state: {
    pageID: null,
    activeComponentID: null,
    componentList: [],
    clipBoard: null,
    selectedNodeLocation: {}
  },
  getters: {
    activatedComponentIndex: state => {
      return _.findIndex(
        state.componentList,
        o => o.id === state.activeComponentID
      );
    },
    activatedComponent: (state, getters) => {
      return state.componentList[getters.activatedComponentIndex];
    },
    cloneActivatedComponentInfo: getters => {
      return _.cloneDeep(getters.activatedComponent);
    },
    // 判断当前是否选中Component组件
    selectedComponent: state => {
      return state.activeComponentID !== null;
    },
    /**
     * 通过ID获取Component对象
     * @param {String} id ID
     * @returns {Object} Component Object
     */
    getComponentByID: state => id => {
      return _.find(state.componentList, o => o.id === id);
    },
    /**
     * 通过index获取Component对象
     * @param {String} id  Component ID
     * @returns {Number} Component在ComponentList中的下标
     */
    getComponentIndexByID: state => id => {
      return _.findIndex(state.componentList, o => o.id === id);
    }
  },
  mutations: {
    updatePageID(state, id) {
      state.pageID = id;
      console.log("Page ID:", id);
    },
    updateSelectedNodeLocation(state) {
      let node = document.getElementById(state.activeComponentID);
      let rect = node.getBoundingClientRect();
      state.selectedNodeLocation = {
        heigth: rect.height,
        width: rect.width,
        top: rect.top,
        left: rect.left
      };
    },
    /**
     * ViewPort用以更新Component位置
     * @param {Object} state Store state
     * @param {Array} payload NewComponentList
     */
    updateCompList(state, payload) {
      state.componentList = payload;
    },
    /**
     * 更新当前选中的Component ID
     * @param {Object} state Store state
     * @param {String} id Selected Component ID
     */
    updateActiveComponent(state, id) {
      state.activeComponentID = id;
    },
    /**
     * 隐藏右侧组件样式修改框
     * @param {Object} state Store state
     */
    hideStyleModifacationCard(state) {
      state.activeComponentID = null;
    },
    /**
     * 增加新的组件到ComponentList末尾
     * @param {Object} state Store state
     * @param {Object} component Component 对象
     */
    pushComponent(state, component) {
      state.componentList.push(component);
    },
    /**
     * 替换componentInfo
     * @param {Object} state  Store state
     * @param {Object} Payload  包含ID以及Component对象的对象
     * @param {String} Payload.id  ID
     * @param {Object} Payload.componentInfo  Component相关信息
     */
    replaceComponentInfoByID(state, { id, componentInfo }) {
      let component = this.getters["componentStore/getComponentByID"](id);
      Vue.set(component, "componentInfo", componentInfo);
    },
    /**
     * 通过index移除Component
     * @param {Object} state  Store state
     * @param {String} index  Component index
     */
    spliceComponentByIndex(state, index) {
      state.componentList.splice(index, 1);
    },
    /**
     *  Component互换
     * @param {Object} state Store state
     * @param {Object} payload 包含需要换位的两个index的对象
     * @param {Number} payload.sourceIndex index
     * @param {Number} payload.targetIndex index
     */
    componentExchange(state, payload) {
      let { sourceIndex, targetIndex } = payload;
      let arr = state.componentList;
      arr.splice(
        targetIndex,
        1,
        ...arr.splice(sourceIndex, 1, arr[targetIndex])
      );
    },
    /**
     * 添加Component到指定index
     * @param {Object} state Store state
     * @param {Object} payload 包含index和Component Object的对象
     * @param {Number} payload.index index
     * @param {Object} payload.component Component OBject
     */
    addComponentByIndex(state, payload) {
      let { index, component } = payload;
      state.componentList.splice(index, 1, component);
    },
    /**
     * 更新剪贴板
     * @param {Object} state Store State
     * @param {?Object} payload ClipBoard数据
     */
    updateClipBoard(state, payload) {
      state.clipBoard = payload;
    },
    /**
     * 替换ComponentList
     * @param {Object} state Store State
     * @param {Array} payload 新的ComponentList
     */
    replaceComponentList(state, payload) {
      state.componentList = payload;
    }
  },
  actions: {
    /**
     * 通过ID移除组件
     * @param {Object} Store  Store Context
     * @param {String} id Component ID
     */
    removeComponent({ dispatch, commit, getters, state }, id) {
      let index = getters.getComponentIndexByID(id);
      // 创建remove事件
      let event = {
        type: "remove",
        index: index,
        id: id,
        component: _.cloneDeep(getters.getComponentByID(id))
      };
      // remove事件入栈
      dispatch("eventStackStore/eventProcess", event, { root: true });
      commit("spliceComponentByIndex", index);
      // 移除组件后更新右侧样式修改栏状态
      commit("hideStyleModifacationCard");
    },
    /**
     * 更新componentInfo
     * @param {Object} Store  Store Context
     * @param {Object} Payload  里面包含新的ComponentInfo对象以及目标组件ID
     * @param {String} payload.id 目标组件ID
     * @param {Object} payload.value 新的label对象
     */
    updateComponentInfo({ dispatch, getters, commit, state }, { id, value }) {
      window.console.log("updateComponentInfo:", id, value);
      let oldComponentInfo = _.cloneDeep(
        getters.getComponentByID(id).componentInfo
      );
      let newComponentInfo = _.merge(
        _.cloneDeep(getters.getComponentByID(id).componentInfo),
        value
      );
      // 创建update事件
      let event = {
        type: "update",
        id: id,
        newComponentInfo: newComponentInfo,
        oldComponentInfo: oldComponentInfo
      };
      // update事件入栈
      dispatch("eventStackStore/eventProcess", event, { root: true });
      commit("replaceComponentInfoByID", {
        id: id,
        componentInfo: newComponentInfo
      });
    },
    /**
     * 添加component到ViewPort
     * @param {Object} Store Store Context
     * @param {Object} payload 含有ComponentObject targetIndex的对象
     * @param {Object} payload.file Component Object
     * @param {Object} payload.styleModificationBox StyleMenu Object
     * @param {Object} payload.componentInfo componentInfo Object
     */
    async addComponent({ dispatch, commit, state }, payload) {
      let targetIndex = payload.targetIndex;
      let component = {
        desc: payload.componentObject.desc,
        image: payload.componentObject.image,
        name: payload.componentObject.name,
        componentFile: payload.componentObject.file,
        id: uuid(),
        styleMenuID: payload.componentObject.styleMenuID,
        // TODO *** componentInfo应该改为初始化时就进行加载。
        componentInfo: payload.componentObject.componentInfo
      };
      // 创建add事件
      let event = {
        type: "add",
        id: component.id,
        index: targetIndex,
        component: component
      };
      // add事件入栈
      dispatch("eventStackStore/eventProcess", event, { root: true });
      commit("addComponentByIndex", {
        component: component,
        index: targetIndex
      });
    },
    /**
     * 复制Component
     * @param {Object} Store Store Object
     */
    copyComponent({ getters, commit }) {
      commit("updateClipBoard", getters.activatedComponent);
    },
    /**
     * 粘贴Component
     * @param {Object} Store Store Object
     */
    pasteComponent({ state, dispatch, commit }) {
      // 需要复制后才能更改
      let component = _.cloneDeep(state.clipBoard);
      // ID不能重复，需要重新生成
      component.id = uuid();
      // 创建add事件(虽然是Paste，但是本质上还是add事件)
      let event = {
        type: "add",
        id: component.id,
        index: state.componentList.length,
        component: component
      };
      // add事件入栈
      dispatch("eventStackStore/eventProcess", event, { root: true });
      commit("pushComponent", component);
      commit("hideStyleModifacationCard");
    },
    resetStore({ state }) {
      state.pageID = null;
      state.activeComponentID = null;
      state.componentList = [];
      state.clipBoard = null;
      state.selectedNodeLocation = {};
    },
    updateStore({ commit }, payload) {
      commit("updatePageID", payload.id);
      commit("updateCompList", payload.data);
    }
  }
};
