<template>
  <div class="center-view" :style="scaleObject">
    <!-- <v-contextmenu ref="contextmenu">
      <v-contextmenu-item @click="removeButtonClick">Remove</v-contextmenu-item>
    </v-contextmenu> -->
    <div class="viewport" id="viewport">
      <draggable
        v-model="componentList"
        group="component"
        @start="drag = true"
        @end="drag = false"
        v-bind="dragOptions"
        @choose="onChoose"
        @change="onChange"
        :emptyInsertThreshold="100"
      >
        <transition-group type="transition" :name="!drag ? 'flip-list' : null">
          <component
            v-for="comp in componentList"
            :key="comp.id"
            :is="comp.componentFile"
            :comp="comp"
            tabindex="-1"
            class="group-item"
            :class="{ selected: activeComponentID === comp.id }"
          ></component>
        </transition-group>
      </draggable>
      <!-- <SelectedBorder /> -->
    </div>
  </div>
</template>

<script>
import HTTP from "@/util/HTTP.js";
const getComponentInfoList = HTTP.getComponentInfoList;
import { createNamespacedHelpers } from "vuex";
const { mapMutations, mapState, mapActions } = createNamespacedHelpers(
  "componentStore"
);
import draggable from "vuedraggable";

export default {
  name: "ViewPort",
  components: {
    draggable
    // SelectedBorder
  },
  data() {
    return {
      drag: false,
      scaleObject: {
        transform: "scale(1)"
      }
    };
  },
  methods: {
    ...mapMutations([
      "updateActiveComponent",
      "hideStyleModifacationCard",
      "updateCompList",
      "updateComponentInfo",
      "replaceComponentList",
      "updateSelectedNodeLocation"
    ]),
    ...mapActions(["removeComponent"]),
    removeButtonClick() {
      if (this.activeComponentID) {
        this.removeComponent(this.activeComponentID);
      }
    },
    /**
     * @param {Object} e event事件
     */
    onChange(e) {
      // 根据事件类型调用相对应的方法
      if (e.added) {
        this.onAdd(e);
      } else if (e.moved) {
        this.onMove(e);
      }
    },
    /**
     * @param {Object} e Event
     * @param {String} e.type 事件类型 此处应为added
     * @param {Object} e.element added事件对象，此处应为ComponentObject
     * @param {Number} e.newIndex 目标下标
     */
    onAdd(e) {
      let payload = {
        targetIndex: e.added.newIndex,
        componentObject: e.added.element
      };
      this.$store.dispatch("componentStore/addComponent", payload);
    },
    // 选中Component时调用
    onChoose(e) {
      let id = e.item.id;
      this.updateActiveComponent(id);
      this.updateSelectedNodeLocation();
    },
    /**
     * @param {Object} e Custom Event
     * @param {String} e.type 事件类型 此处只监听moved
     * @param {Object} e.element moved事件对象 此处为ComponentList里的对象
     * @param {Number} e.newIndex 移动的新座标
     * @param {Number} e.oldIndex 移动的旧座标
     */
    onMove(e) {
      // 创建move事件
      let event = {
        type: "move",
        id: e.moved.element.id,
        targetIndex: e.moved.newIndex,
        sourceIndex: e.moved.oldIndex
      };
      // move事件入栈
      this.$store.dispatch("eventStackStore/eventProcess", event);
    }
  },
  computed: {
    ...mapState(["activeComponentID"]),
    componentList: {
      get() {
        return this.$store.state.componentStore.componentList;
      },
      set(v) {
        this.updateCompList(v);
      }
    },
    dragOptions() {
      return {
        animation: 200,
        group: "description",
        disabled: false,
        ghostClass: "ghost"
      };
    },
    scale() {
      return this.$store.state.menuStore.rateValue / 100;
    }
  },
  watch: {
    scale(val) {
      this.scaleObject = {
        transform: `scale(${val})`
      };
    }
  },
  mounted() {
    if (this.$route.params.backupID) {
      getComponentInfoList(this.$route.params.backupID).then(res => {
        console.log(res.data.result);
        this.replaceComponentList(JSON.parse(res.data.result.jsonData));
      });
    }
    // 创建页面
    this.$store.dispatch("pageStore/createPage");
    // 选中页面
    this.$store.dispatch("pageStore/selectedPage", 0);
  }
};
</script>

<style lang="stylus" scoped>
.center-view
  position relative
  display flex
  justify-content center
  align-items center
  height 100%
.assistor
  position relative
  display block
.viewport
  height 667px
  width 375px
  overflow-y scroll
  background-color #fff
  transform translateZ(0)
  scrollbar-width none
  -ms-overflow-style none
  &::-webkit-scrollbar
    display none
.group-item
  cursor pointer
  :focus
    outline 1px dashed green
.selected
  border 1px solid black
.flip-list-move
  transition transform 0.5s
.no-move
  transition transform 0s
.ghost
  opacity 0.5
  background #c8ebfb
.list-group
  min-height 20px
.list-group-item
  cursor move
.list-group-item i
  cursor pointer
.element::-webkit-scrollbar
  display:none;
</style>
