<script>
import Zoom from "./Zoom.vue";
import Alignment from "./Alignment.vue";
import HTTP from "@/util/HTTP.js";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions } = createNamespacedHelpers("eventStackStore");
const saveComponentInfoList = HTTP.saveComponentInfoList;
export default {
  components: {


    Zoom,
    Alignment
  },
  data() {
    return {
      showAlignmentComponent: false
    };
  },
  methods: {
    ...mapActions(["undo", "redo"]),

    undoClick() {
      this.undo();
    },
    redoClick() {
      this.redo();
    },
    copyClick() {
      // 判断当前是否选中Component
      // XXX: 后期将这个判断放在样式判断里，如果未选中则直接无法点击
      if (this.selectedComponent) {
        this.$store.dispatch("componentStore/copyComponent");
      }
    },
    pasteClick() {
      // 判断剪贴板是否有内容
      // XXX: 后期将这个判断放在样式判断里，如果未选中则直接无法点击
      if (this.hasContent) {
        this.$store.dispatch("componentStore/pasteComponent");
      }
    },
    alignmentClick() {
      this.showAlignmentComponent = !this.showAlignmentComponent;
    },
    saveComponent() {
      console.log(
        JSON.stringify(this.$store.state.componentStore.componentList)
      );
      saveComponentInfoList(
        JSON.stringify(this.$store.state.componentStore.componentList)
      ).then(res => {
        console.log(res);
      });
    }
  },
  computed: {
    ...mapState(["stack", "current"]),
    // 判断当前是否选中Component
    selectedComponent() {
      return this.$store.getters["componentStore/selectedComponent"];
    },
    // 判断剪贴板是否有内容
    hasContent() {
      return this.$store.state.componentStore.clipBoard !== null;
    },
    toBegin() {
      // current等于0则无法Click
      return this.current >= 0;
    },
    toEnd() {
      // current大于Stack Length则无法Click
      return this.current < this.stack.length - 1;
    }
  }
};
</script>
