import util from "@/util/index.js";
import Vue from "vue";
const { uuid } = util;
const _ = require("lodash");
export default {
  namespaced: true,
  state: {
    // 页面列表
    pageList: [],
    selectedPageIndex: 0
  },
  getters: {
    selectedPage: state => state.pageList[state.selectedPageIndex],
    findPageIndexByID: state => id =>
      state.pageList.findIndex(page => page.id === id)
  },
  mutations: {
    removePage(state, id) {
      state.pageList = state.pageList.filter(page => {
        return page.id !== id;
      });
    },
    pushPage(state, payload) {
      state.pageList.push(payload);
    },
    updateImageData(state, imageData) {
      Vue.set(
        state.pageList[state.selectedPageIndex],
        "previewImage",
        imageData
      );
    },
    updateSelectedIndex(state, index) {
      state.selectedPageIndex = index;
    },
    updatePageData(state, data) {
      state.pageList[state.selectedPageIndex]["data"] = data;
    }
  },
  actions: {
    createPage({ commit }) {
      let id = uuid();
      let page = {
        id: id,
        desc: "",
        previewImage: "",
        data: []
      };
      commit("pushPage", page);
    },
    deletePage({ dispatch, commit, state }, { id }) {
      let targetIndex = this.getters["pageStore/findPageIndexByID"](id);
      commit("removePage", id);
      // 如果需要删除当前页面则重新选中页面
      if (targetIndex === state.selectedPageIndex) {
        // 防止Index出现负数
        let index = targetIndex === 0 ? 0 : state.selectedPageIndex - 1;
        dispatch("selectedPage", index);
      }
    },
    updatePreviewImage({ commit }, imageData) {
      commit("updateImageData", imageData);
    },
    selectedPage({ commit, state, dispatch }, index) {
      // 重置Undo Redo Stack
      commit("eventStackStore/resetStack", null, {
        root: true
      });
      // 重置ComponentStore
      dispatch("componentStore/resetStore", null, {
        root: true
      });
      // 更新选中Index
      commit("updateSelectedIndex", index);
      dispatch("componentStore/updateStore", state.pageList[index], {
        root: true
      });
    }
  }
};
