<template>
  <div class="alignment-wrapper">
    <a-icon
      v-for="(icon, index) in icons"
      :key="index"
      :component="icon.src"
      style="color: #7d8694;"
      :class="{ 'header-unclickable-button': checkOperational(icon.name) }"
      @click="changeAlign(icon.name)"
    />
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions, mapGetters } = createNamespacedHelpers(
  "componentStore"
);
// import horizontalLeft from "@/assets/icon/header/horizontal-left.svg"
export default {
  data() {
    return {
      icons: [
        {
          name: "horizontal-left",
          class: "header-clickable-button",
          src: () => import("@/assets/icon/header/horizontal-left.svg")
        },
        {
          name: "horizontal-center",
          class: "header-clickable-button",
          src: () => import("@/assets/icon/header/horizontal-center.svg")
        },
        {
          name: "horizontal-right",
          class: "header-clickable-button",
          src: () => import("@/assets/icon/header/horizontal-right.svg")
        },
        {
          name: "vertical-top",
          class: "header-clickable-button",
          src: () => import("@/assets/icon/header/vertical-top.svg")
        },
        {
          name: "vertical-center",
          class: "header-clickable-button",
          src: () => import("@/assets/icon/header/vertical-center.svg")
        },
        {
          name: "vertical-bottom",
          class: "header-clickable-button",
          src: () => import("@/assets/icon/header/vertical-bottom.svg")
        }
      ]
    };
  },
  methods: {
    ...mapActions(["updateComponentInfo"]),
    /**
     * 检查该组件可以对齐的方向
     * @param {String} align 方向名称
     * @return {Boolean}
     */
    checkOperational(align) {
      return !this.getOperationalAlign().includes(align);
    },
    /**
     * 获取可对齐的方向
     * @return {Array} 方向数组
     */
    getOperationalAlign() {
      return this.activatedComponent.componentInfo.operational.align;
    },
    changeAlign(align) {
      //调用策略
      // TODO FIX 暂时只有Text组件支持
      inlineElementChangeStrategy[align](this);
    }
  },
  computed: {
    ...mapState(["activeComponentID"]),
    ...mapGetters(["activatedComponent"])
  }
};
const inlineElementChangeStrategy = {
  "horizontal-left": that => {
    let payload = {
      id: that.activeComponentID,
      value: {
        style: {
          "text-align": "left"
        }
      }
    };
    that.updateComponentInfo(payload);
  },
  "horizontal-center": that => {
    let payload = {
      id: that.activeComponentID,
      value: {
        style: {
          "text-align": "center"
        }
      }
    };
    that.updateComponentInfo(payload);
  },
  "horizontal-right": that => {
    let payload = {
      id: that.activeComponentID,
      value: {
        style: {
          "text-align": "right"
        }
      }
    };
    that.updateComponentInfo(payload);
  }
};
</script>
<style lang="stylus" scoped>
