  position absolute
  top 40px
  left -35%
  width 130px
  height 88px
  display grid
  grid-template-columns 1fr 1fr 1fr
  grid-template-rows 1fr 1fr
  justify-items center
  align-items center
  background-color white
  border-radius 8px
  box-shadow 3px 3px 14px -8px rgba(0,0,0,0.75)
  cursor default
  &:before
    display block
    content ''
    border-width 8px 8px 8px 8px
    border-style solid
    border-color transparent transparent #fff transparent
    box-shadow 3px 3px 14px -8px rgba(0,0,0,0.75)
    position absolute
    left 43%
    top -16px
  svg
    cursor pointer
  img
    cursor pointer
</style>
