import Vue from "vue";
import Vuex from "vuex";
import menuStore from "./moudel/menuStore";
import componentStore from "./moudel/componentStore";
import eventStackStore from "./moudel/eventStackStore";
import pageStore from "./moudel/pageStore";
const _ = require("lodash");
import html2canvas from "html2canvas";

Vue.use(Vuex);

const backupComponentDataPlugin = store => {
  let types = [
    "componentStore/updateCompList",
    "componentStore/pushComponent",
    "componentStore/replaceComponentInfoByID",
    "componentStore/spliceComponentByIndex",
    "componentStore/componentExchange",
    "componentStore/replaceComponentList"
  ];
  const debounceGeneratePreviewImage = _.debounce(() => {
    html2canvas(document.getElementById("viewport")).then(canvas => {
      store.commit("pageStore/updateImageData", canvas.toDataURL());
    });
  }, 5000);
  store.subscribe(mutation => {
    if (types.some(type => type === mutation.type)) {
      // 备份组件数据
      store.commit(
        "pageStore/updatePageData",
        store.state.componentStore.componentList
      );
      // 受限于性能因素，预览图避免实时生成
      debounceGeneratePreviewImage();
    }
  });
};

const Store = new Vuex.Store({
  plugins: [backupComponentDataPlugin],
  modules: {
    menuStore,
    componentStore,
    eventStackStore,
    pageStore
  }
});

export default Store;
