import Vue from "vue";
import Router from "vue-router";
import Index from "./views/Index.vue";
// import Sider from "./components/Sider.vue";

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "home",
      component: Index
    },
    {
      path: "/:backupID",
      name: "home",
      component: Index
    }
  ]
});
