import axios from "axios";
const URL = "http://192.168.1.163/phoneex/public/api/test";
const saveComponentInfoList = componentList =>
  axios.post(URL, {
    jsonData: componentList
  });
const getComponentInfoList = id =>
  axios.get(URL, {
    params: {
      id: id
    }
  });
export default {
  saveComponentInfoList,
  getComponentInfoList
};
