<template>
  <div class="zoom-wrapper">
    <div>
      <svg
        @click.stop="lessen"
        t="1568086991462"
        class="icon"
        viewBox="0 0 1024 1024"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        p-id="1869"
        width="16"
        height="16"
      >
        <path d="M800 479v66H224v-66z" p-id="1870" fill="#8a8a8a" />
      </svg>
      <span>{{ rate }}%</span>
      <svg
        @click.stop="plus"
        t="1568087011678"
        class="icon"
        viewBox="0 0 1024 1024"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        p-id="2718"
        width="16"
        height="16"
      >
        <path
          d="M469.333333 213.333333v256H213.333333v85.333334h256v256h85.333334v-256h256v-85.333334h-256V213.333333z"
          p-id="2719"
          fill="#8a8a8a"
        />
      </svg>
    </div>
    <span>缩放</span>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapMutations } = createNamespacedHelpers("menuStore");
export default {
  methods: {
    ...mapMutations(["updateRateValue"]),
    lessen() {
      this.rate -= 5;
    },
    plus() {
      this.rate += 5;
    }
  },
  computed: {
    rate: {
      get() {
        return this.$store.state.menuStore.rateValue;
      },
      set(v) {
        this.updateRateValue(v);
      }
    }
  }
};
</script>
