<template>
  <div v-show="showElement">
    <div class="header">
      <div>页面列表</div>
      <div><img src="@/assets/icon/page_help.png" /></div>
    </div>
    <div class="page-wrap">
      <div class="page" v-for="(page, index) in pageList" :key="index">
        <div class="left-action-wrap">
          <div class="icon-move-up"></div>
          <span>{{ index + 1 }}</span>
          <div class="icon-move-down"></div>
        </div>
        <div class="center-preview-wrap">
          <div class="preview-image" @click="selectedPage(index)">
            <img :src="page.previewImage" alt="" />
          </div>
          <span>首页</span>
        </div>
        <div class="right-action-wrap">
          <div class="icon-edit"></div>
          <div class="icon-copy"></div>
          <div class="icon-delete" @click="deletePage(page)"></div>
        </div>
      </div>
    </div>
    <div class="new-page-action-wrap">
      <div class="action" @click="createPage">
        <img src="@/assets/icon/page_newpage.png" alt="" />
        <span>新建页面</span>
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapGetters, mapState, mapMutations } = createNamespacedHelpers(
  "menuStore"
);
export default {
  name: "element",
  data() {
    return {
      activeIndex: null
    };
  },
  methods: {
    createPage() {
      this.$store.dispatch("pageStore/createPage");
    },
    deletePage(id) {
      // 当只剩下最后一个页面的时候，首先创建一个新页面
      if (this.$store.state.pageStore.pageList.length === 1) {
        this.createPage();
      }
      this.$store.dispatch("pageStore/deletePage", id);
    },
    selectedPage(index) {
      // 避免重复调用
      if (index !== this.$store.state.pageStore.selectedPageIndex) {
        this.$store.dispatch("pageStore/selectedPage", index);
      }
    }
  },
  computed: {
    ...mapGetters(["showDisplayCard"]),
    ...mapState(["activeMenuIndex", "componentMenu"]),
    showElement() {
      return this.activeMenuIndex === 1;
    },
    activeComponentID() {
      return this.$store.state.componentStore.activeComponentID;
    },
    pageList() {
      return this.$store.state.pageStore.pageList;
    },
    selectedPageIndex() {
      return this.$store.state.pageStore.selectedPageIndex;
    }
  }
};
</script>

