<template>
  <div v-show="showElement">
    <div class="header">
      <div>页面元素</div>
      <div></div>
    </div>
    <div class="element-wrap" v-if="pageComponets.length !== 0">
      <div
        class="element"
        v-for="(element, index) in pageComponets"
        :key="index"
        :class="{ active: activeComponentID === element.id }"
        @click="onChoose(element)"
      >
        <div class="desc-wrap">
          <img :src="element.image" />
          <span>{{ element.desc }}</span>
        </div>
        <div class="actions">
          <div class="display" @click.stop="changeDisplay(element)"></div>
          <div class="edit"></div>
          <div class="delete" @click.stop="deleteComponent(element)"></div>
          <div class="change-index"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapGetters, mapState, mapMutations } = createNamespacedHelpers(
  "menuStore"
);
export default {
  name: "element",
  data() {
    return {
      activeIndex: null
    };
  },
  methods: {
    // changeActiveIndex(index){
    //   if(this.activeIndex !== index){
    //     this.activeIndex = index
    //   } else {
    //     this.activeIndex = null
    //   }
    // },
    onChoose(element) {
      this.$store.commit("componentStore/updateActiveComponent", element.id);
    },
    changeDisplay(element) {
      let component = this.$store.getters["componentStore/getComponentByID"](
        element.id
      );
      let displayStatus =
        component.componentInfo.style.display === "none" ? false : true;
      let payload = {
        id: element.id,
        value: {
          style: {
            display: displayStatus ? "none" : "block"
          }
        }
      };
      this.$store.dispatch("componentStore/updateComponentInfo", payload);
    },
    deleteComponent(element) {
      this.$store.dispatch("componentStore/removeComponent", element.id);
    }
  },
  computed: {
    ...mapGetters(["showDisplayCard"]),
    ...mapState(["activeMenuIndex", "componentMenu"]),
    showElement() {
      return this.activeMenuIndex === 2;
    },
    pageComponets() {
      return this.$store.state.componentStore.componentList;
    },
    activeComponentID() {
      return this.$store.state.componentStore.activeComponentID;
    }
  }
};
</script>

