<template>
  <div class="options-wrap" v-if="activeComponentID" :style="offsetTop">
    <div class="action delete" @click="deleteComponent">
      <div></div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions } = createNamespacedHelpers("componentStore");
export default {
  data() {
    return {
      viewportOffsetTop: ""
    };
  },
  computed: {
    ...mapState(["activeComponentID", "selectedNodeLocation"]),
    offsetTop() {
      let rect = this.getSelectedNodeRect();
      return {
        top: `${rect.top + rect.height / 2}px`,
        transform: "translateY(-50%)"
      };
    }
  },
  methods: {
    ...mapActions(["removeComponent"]),
    deleteComponent() {
      this.removeComponent(this.activeComponentID);
    },
    getSelectedNodeRect() {
      return document
        .getElementById(this.activeComponentID)
        .getBoundingClientRect();
    }
  },
  watch: {},
  mounted() {
    let viewportNode = document.getElementById("viewport");
    this.viewportOffsetTop = viewportNode.offsetTop;
  }
};
</script>

